import { message } from 'antd';


//这是action

// const togglelogin = {
//   type: 'toggleAuth'
// }
//这是reducer
const reducer = (state = logindata, action) => {
  switch (action.type) {
    case 'toggleAuth':
      let user = action.user
      console.log(action)
      return state = {
        auth: true,
        user
      };
    case 'loginOut':
      return state = {
        auth: false,
        user: {}
      };
    case 'success':
      message.success(action || "操作成功");
      return state;
    case 'error':
      message.error(action || "操作错误");
      return state;
    case 'warning':
      message.warning(action || "操作警告");
      return state;
    default:
      return state;
  }
}
export default reducer;